// app/layout.tsx
import "./css/style.css";

import { Inter } from "next/font/google";
import localFont from "next/font/local";

import Header from "@/components/ui/header";
import Chat from "@/components/Chat";
import FAQIcon from "@/components/FAQIcon";
import DraggableWrapper from "../components/ui/DraggableWrapper"; // Use relative path

const inter = Inter({
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap",
});

const nacelle = localFont({
  src: [
    { path: "../public/fonts/nacelle-regular.woff2", weight: "400", style: "normal" },
    { path: "../public/fonts/nacelle-italic.woff2", weight: "400", style: "italic" },
    { path: "../public/fonts/nacelle-semibold.woff2", weight: "600", style: "normal" },
    { path: "../public/fonts/nacelle-semibolditalic.woff2", weight: "600", style: "italic" },
  ],
  variable: "--font-nacelle",
  display: "swap",
});

export const metadata = {
  title: "Monaarch",
  description: "Created By NisaEngineers",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body
        className={`${inter.variable} ${nacelle.variable} bg-gray-950 font-inter text-base text-gray-200 antialiased`}
      >
        {/* Ensure this container is relatively positioned */}
        <div className="relative flex min-h-screen flex-col overflow-hidden supports-[overflow:clip]:overflow-clip">
          <Header />
          {/* Wrap Chat and FAQIcon in our draggable wrapper */}
          <DraggableWrapper>
            <Chat />
          </DraggableWrapper>
          <DraggableWrapper>
            <FAQIcon />
          </DraggableWrapper>
          {children}
        </div>
      </body>
    </html>
  );
}
